@charset "UTF-8";

:root{
  --bryntum-version:"6.1.5";
}

.b-fa{
  font-family:var(--b-fa-style-family, "Font Awesome 6 Free");
  font-weight:var(--b-fa-style, 900);
}

.b-fa-solid,
.b-fa-regular,
.b-fa-brands,
.fas,
.far,
.fab,
.b-fa-sharp-solid,
.b-fa-classic,
.b-fa{
  -moz-osx-font-smoothing:grayscale;
  -webkit-font-smoothing:antialiased;
  display:var(--b-fa-display, inline-block);
  font-style:normal;
  font-variant:normal;
  line-height:1;
  text-rendering:auto;
}

.fas,
.b-fa-classic,
.b-fa-solid,
.far,
.b-fa-regular{
  font-family:"Font Awesome 6 Free";
}

.fab,
.b-fa-brands{
